.main {
  width: 100%;
  height: 800px;

  background-image: url('/img/images/bg_img.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__inner {
    padding-top: 200px;
  }

  &__block {
    padding: 10px 20px;
  }

  &__genres {
    color: #fff;
  }

  &__feed {
    color: #fff;
  }

  &__title {
    margin-bottom: 20px;
    font-size: 20px;
  }

  &__genre {
    margin-bottom: 10px;
    font-size: 16px;
    color: #fff;
  }

  &__image {
    width: 150px;
    height: 250px;
  }

  &__card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    border-bottom: 2px solid #000;
  }

  &__card:last-child {
    border-bottom: none;
  }

  &__info {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    text-align: justify;
  }

  &__filmtitle {
    margin-bottom: 15px;
    font-size: 25px;
  }

  &__description {
    margin-bottom: 20px;
    font-size: 16px;
  }

  &__rating {
    align-self: end;
    font-size: 20px;
  }
}

