.header {
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  

  &__inner {
    display: flex;
    justify-content: space-between;
    height: 100px;
    padding: 0 20px;
  }

  &__logo {
    align-self: center;
  }

  &__finder {
    display: flex;
    position: relative;
    width: 300px;
    margin: 0 auto;
    height: 0;

    &--input {
      width: 100%;
      height: 0;
      padding-left: 10px;
      border: 2px solid #7BA7AB;
      border-radius: 5px;
      outline: none;
      background: #F9F0DA;
      color: #9E9C9C;
    }

    &--btn {
      position: absolute; 
      top: 15px;
      right: 0px;
      width: 42px;
      height: 42px;
      border: none;
      background: #7BA7AB;
      border-radius: 0 5px 5px 0;
      cursor: pointer;
    }

    &--btn::after {
      content: "\f002";
      font-family: FontAwesome;
      font-size: 16px;
      color: #F9F0DA;
    }
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 45%;
    height: 100%;
    background-color: inherit;
    box-shadow: none;

    &--list {
      display: flex;
      justify-content: space-between;
      width: 100%;
    }

    &--link {
      padding: 0;
      font-size: 16px;
    }
  }
}